import React from 'react';
//import TestingForm from '../components/TestingForm';
import TestingGuide from '../components/TestingGuide';
import Statistic from '../components/Statistic';
import Word from '../components/Word';
import AddWord from '../components/AddWord';

import base from '../base';




class App extends React.Component {
    constructor() {
        super();

        this.addVocabulary = this.addVocabulary.bind(this);
        this.updateWord = this.updateWord.bind(this);

        this.state = {
            vocabulary: {},
            statistic: {},
            symbols: "yxcvbnmasdfghjklqwertzuiop,.(){}-;+YXCVBNMASDFGHJKLQWERTZUIOP+"
        }
    }

    addVocabulary(Word) {
        const vocabulary = {...this.state.vocabulary};
        const timestamp = Date.now();

        vocabulary[`key-${timestamp}`] = Word;
        this.setState({vocabulary});
    }
    updateWord(key, updatedWord){
       const vocabulary={...this.state.vocabulary};
       vocabulary[key]=updatedWord;
       console.log(vocabulary);
        console.log(updatedWord);
       this.setState({vocabulary});

    }

    componentWillMount(){
        this.ref=base.syncState(`vocabulary`, {
            context: this,
            state:'vocabulary'
        });
    }

    componentWillUnmount(){
        base.removeBinding(this.ref);
    }


    render() {
        return (
            <div className="container">
                <div className="row">
                    <TestingGuide />

                    <Statistic />

                    <div className="col-md-4 col-sm-12 addVocabulary-box">
                        <h2>Pidat slovko</h2>
                        <AddWord addVocabulary={this.addVocabulary}/>

                    </div>
                </div>
                <div className="row">
                    <h2>Slovka v databzi</h2>
                    <p>(Barevn oznaen slovka obsahuj esk znaky!)</p>
                    <ul className="vocabulary-list">
                        {
                            Object
                                .keys(this.state.vocabulary)
                                .map(key => <Word key={key} index={key} details={this.state.vocabulary[key]} updateWord={this.updateWord}/>)
                        }

                    </ul>
                </div>
                <div className="row todo-list">
                <h2>TODO:</h2>
                <ul>
                    <li><span className="glyphicon glyphicon-ok"></span> &nbsp; detekce eskch znak pi idvn do slovnku</li>
                    <li> <span className="glyphicon glyphicon-remove" ></span> &nbsp; editace poloek v databzi</li>
                    <li> <span className="glyphicon glyphicon-remove"></span> &nbsp; mazn poloek v databzi</li>
                    <li> <span className="glyphicon glyphicon-remove"></span> &nbsp; kontrola duplicit (nebude)</li>

                </ul>

                </div>
            </div>

        )
    }
}

export default App;


